import React, { useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import styled from 'styled-components';

const Container = styled.div`
  /* border-radius: 5px; */
  position: relative;
  /* box-sizing: border-box; */
`;

const Title = styled.div`
  color: white;
  font-size: 10pt;
  position: absolute;
  bottom: 0;
  right: 0;
`;

const Error = styled.div`
  color: tomato;
  font-size: large;
  position: absolute;
  display: none;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
`;

const getPlayerHeight = (w) => {
  return Math.round((w / 16) * 9);
};

const CctvPlayer = ({ name, url }) => {
  const [width, setWidth] = useState((document.body.clientWidth - 20) / 4);
  const [height, setHeight] = useState(getPlayerHeight(width));

  const doRefresh = () => {
    setWidth((document.body.clientWidth - 100) / 4);
    setHeight(getPlayerHeight(width));
  };

  useEffect(() => {
    window.addEventListener('resize', doRefresh);
    return window.removeEventListener('resize', doRefresh);
  });

  const onPlayerError = () => {
    console.log(name, url);
  };
  return (
    <Container>
      <Title>{name}</Title>
      <Error id="error">이용할수 없습니다.</Error>
      <ReactPlayer
        url={url}
        playing={true}
        muted={true}
        width={width}
        height={height}
        onError={onPlayerError}
      />
    </Container>
  );
};

export default CctvPlayer;
